body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:lightgray}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.primary-button,.secondary-button{border:2px solid black;border-radius:5px;padding:10px 25px;margin:5px;font-size:20px;font-weight:bold;text-decoration:none}.primary-button:hover,.secondary-button:hover{background-color:deeppink;cursor:pointer}.primary-button{color:cyan;background-color:deeppink}.primary-button:hover{background-color:hotpink}.secondary-button{color:purple;background-color:cyan}.secondary-button:hover{background-color:deepskyblue}

.home{text-align:center}.home .race-tile-container{display:grid;grid-template-columns:1fr 1fr 1fr;margin:0 340px}

.race-tile{border:1px solid black;border-radius:10px;margin:10px;padding:20px 0;font-size:25px;font-weight:bold}.race-tile:hover{background-color:lightgray;cursor:pointer}

.nav-bar{text-align:center}.nav-bar .unicorn-banner{height:400px}

.race-page{margin-top:30px}.race-page .race-controls{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;justify-items:center;text-align:center;margin:30px 320px}.race-page .race-controls .button{margin:5px 15px}.race-page .header-row{display:grid;grid-template-columns:1fr 1fr;align-items:center;justify-items:center;margin:50px 400px}.race-page .header-row div{font-size:30px;font-weight:bold;text-align:center}.race-page .header-row div .title{font-size:25px;font-style:italic}.race-page .header-row .secondary-button{display:table;height:100px;padding:0;max-width:270px}.race-page .header-row .secondary-button div{display:table-cell;vertical-align:middle;padding:10px 20px;text-align:center;font-weight:bold;font-size:20px}.race-page .course-image{text-align:center}.race-page .course-image img{max-width:700px}.race-page .description-text{margin:50px 300px;font-size:25px}

